















































































































  import addContact from '../../components/addContact.vue'
  import contact from '../../components/contact.vue'
  import rewardRule from '../../components/rewardRule.vue'
  import poster from './poster.vue'
  
	export default {
    components: {
      addContact,
      contact,
      rewardRule,
      poster
    },
		data() {
			return {
				loanInfo: null,
        addData: {},
        contactData: {},
        reqing: false,
        rewardData: {},
        popShareType: false,
        posterData: {},
        shorturl: '',
        clickShareLinking: false
			};
		},
    computed: {
      userInfo() {
        return this.$store.state.userInfo
      },
      isRole() {
        return this.userInfo.role >= 1
      }
    },
		onLoad(query) {
			this.$http({
        url: this.$apis.loanDetails,
        method: 'GET',
        data: {
          id: query.loanId
        },
        cb: (err, data) => {
          if (!err && data.success) {
            this.loanInfo = data.data
            this.$set(this.loanInfo, 'tagArr', this.handleStr(this.loanInfo.tag_ids))
            uni.setNavigationBarTitle({
            	title: this.loanInfo.name
            })
            if (uni.getStorageSync('createLoan') === true) {
              this.gotoHandle()
              uni.removeStorageSync('createLoan')
            }
          } else {
            uni.showToast({
            	title: '获取贷款详情失败',
              icon: 'none'
            })
          }
        }
      })
		},
    onShow() {
      if (uni.getStorageSync('createLoan') === true && this.loanInfo) {
        this.gotoHandle()
        uni.removeStorageSync('createLoan')
      }
    },
    methods: {
      handleStr(str) {
      	return str.split(',')
      },
      gotoAdd() {
        this.addData = {
          show: true,
          productId: this.loanInfo.id,
          link: this.loanInfo.channel_url_link,
          state: 0,
          type: 'dk'
        }
      },
      gotoHandle() {
        if (this.reqing) return
        this.reqing = true
        this.$http({
        	url: this.$apis.contactList,
        	method: 'GET',
        	data: null,
        	cb: (err, data) => {
        		if (err || data.data.data.length <= 0) {
        			this.gotoAdd()
        		} else {
        			this.contactData = {
                show: true,
                productId: this.loanInfo.id,
                link: this.loanInfo.channel_url_link,
                state: 0,
                type: 'dk',
                index: -1
              }
        		}
        		this.reqing = false
        	}
        })
      },
      showRule(item) {
        if (item.commission_type === 0) {
          this.rewardData = {
            show: true,
            total: item.max_price,
            conditionTitle: item.clearing_title,
            conditionDec: item.clearing_info,
            type: 1,
            name: 'dk',
            frPercent: item.rate
          }
        } else {
          this.rewardData = {
            show: true,
            total: item.rate,
            conditionTitle: item.clearing_title,
            conditionDec: item.clearing_info,
            name: 'dk'
          }
        }
      },
      gotoIndex() {
        uni.switchTab({
        	url: '/pages/shop/index'
        })
      },
      gotoGuide() {
        this.$navigateTo({
        	url: '/pages/loan/guide?loanId=' + this.loanInfo.id
        })
      },
      showSharePop() {
        this.popShareType = true
      },
      hideSharePop() {
        this.popShareType = false
      },
      clickShareLink() {
        if (this.clickShareLinking) {
          uni.showToast({
          	title: '分享中，请勿频繁操作',
            icon: 'none'
          })
          return
        }
        this.clickShareLinking = true
        let str = this.$constant.shareUrl + '?pid=' + this.userInfo.id
        uni.getProvider({
        	service: 'share',
          success: res => {
            if (~res.provider.indexOf('weixin')) {
              uni.share({
              	provider: 'weixin',
              	type: 0,
                title: this.$store.state.config.system_nick_name,
                scene: 'WXSceneSession',
                summary: this.$store.state.config.platform_info,
                href: str,
                imageUrl: this.$store.state.config.logo,
              	success: res => {
                  console.log(res)
                },
              	fail: err => {
                  this.copy(str)
                },
              	complete: () => {
                  this.clickShareLinking = false
                }
              });
            } else {
              this.clickShareLinking = false
              this.copy(str)
            }
          },
          fail: () => {
            this.clickShareLinking = false
          	this.copy(str)
          }
        })
      },
      clickSharePoster() {
        this.hideSharePop()
        this.posterData = {
          show: true,
          code: this.loanInfo.name,
          logoUrl: this.loanInfo.logo_url,
          posterUrl: this.loanInfo.background_img,
          url: this.$constant.shareUrl + '?pid=' + this.userInfo.id,
          maxPrice: this.loanInfo.max_price,
          tag: this.loanInfo.tag_ids
        }
      },
      copy(str) {
        uni.setClipboardData({
        	data: str,
          success: () => {
            uni.showModal({
              title: '复制成功',
              content: '链接已复制，请手动粘贴发送给好友',
              showCancel: false
            })
          },
          fail: () => {
            uni.showModal({
              title: '复制失败',
              content: str,
              showCancel: false
            })
          },
          complete: () => {
          	this.hideSharePop()
          }
        })
      }
    }
	}
